import React from 'react';
import './gameSidebar.css';

class GameSidebar extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
		  descriptionLength: 250
		}
		this.readMore = this.readMore.bind(this);
	  }

	readMore= () => {
		this.setState ({
			descriptionLength: this.props.game.fields.gameDescription.length
		})
	}

	componentWillUnmount() {
		this._isMounted = false;
	}

	render() {
		let { gameImage = '', gameTitle = '', gameSlug = '', gameReleaseDate = '', gameDeveloperRef = '', gamePublisherRef = '', gamePlatformRef = '', gameDescription = '', gameAmazon = '' } = this.props.game.fields || {};
		return (
			<React.Fragment>
				{(gameImage !== '', gameTitle !== '', gameSlug !== '', gameDescription !== '', gameAmazon !== '', gameDeveloperRef !== '', gamePublisherRef !== '', gamePlatformRef !== '') ?
					(
					(gameDeveloperRef !== '' ) &&
						(this.developers = gameDeveloperRef.map((developer, key) => {
							return <a className="font-weight-light font-size-md" href={process.env.PUBLIC_URL + "/?p=/studio/" + developer.fields.studioSlug}>{developer.fields.studioName}</a>;
					})),
					(gamePublisherRef !== '' ) &&
						(this.publishers = gamePublisherRef.map((publisher, key) => {
							return <a className="font-weight-light font-size-md" href={process.env.PUBLIC_URL + "/?p=/studio/" + publisher.fields.studioSlug}>{publisher.fields.studioName}</a>;
					})),
					(gamePlatformRef !== '' ) &&
						(this.platforms = gamePlatformRef.map((platform, key) => {
							return <a key={key} className="badge font-weight-light font-size-xs m-1" style={{backgroundColor: platform.fields.platformColour, color: "#ffffff"}} href={process.env.PUBLIC_URL + "/?p=/platform/" + platform.fields.platformSlug}>{platform.fields.platformName}</a>;
					})),
						<div className="widget widget-game" style={{ backgroundColor: '#343538' }}>
							<img src={gameImage.fields.file.url} alt={gameTitle} />
							<div className="widget-body">
								<h4 onClick={e => (window.location.href = process.env.PUBLIC_URL + "/?p=/game/" + gameSlug)} className="widget-title text-white" >{gameTitle}</h4>
								<p className="font-weight-light font-size-md">Released: {(gameReleaseDate !== '') ? (new Intl.DateTimeFormat('en-GB', {
									year: "numeric",
									month: "long",
									day: "2-digit"
								}).format(new Date(gameReleaseDate))) : ('N/A')}</p>
								< h6 > Platforms</h6>
								{this.platforms}
								<h6 className="mt-4">Developer</h6>
								{this.developers}
								<h6 className="mt-4">Publisher</h6>
								{this.publishers}
								{console.log(this.state.descriptionLength)}
								<p className="font-weight-light font-size-md mt-4">{gameDescription.substring(0, this.state.descriptionLength)}{(gameDescription.length > this.state.descriptionLength) && (<span type="button" className="text-white" onClick={this.readMore}>...Read More</span>)}</p>
								<a className="btn btn-outline-light btn-block mt-4" href={gameAmazon} role="button">Purchase <i className="fa fa-shopping-cart"></i></a>
							</div>
						</div>) : (<div></div>)
				}
			</React.Fragment>
		)
	};
}

export default GameSidebar;
