import React from 'react';
import './pagenotfound.css';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class PageNotFound extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      gameIsLoaded: false,
      factIsLoaded: false,
      videoIsLoaded: false,
      games: {},
      facts: {},
      videos: {},
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
  }

  render() {
    return (
      <div className="site-content" role="main">
        <section className="bg-image text-center py-8 px-4 px-md-0" style={{ backgroundColor: '#252525' }}>
          <img className="background" src="https://img.youtube.com/vi/y3Cpetu4ke4/maxresdefault.jpg" alt="" />
          <div className="container">
            <div className="row">
              <div className="col-md-8 col-lg-6 mx-auto">
                <h1 className="display-0 font-weight-black text-white mb-0">404</h1>
                <p className="text-light mb-4">We're sorry, but the page you requested cannot be found.</p>
                <a className="btn btn-primary btn-lg" href={process.env.PUBLIC_URL + '/'}>Back to home</a>
              </div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}

export default PageNotFound;
