import React from 'react';
import './multiFacts.css';
import { Fact } from '..'

class MultiFacts extends React.Component {


	render() {
		let { items = '' } = this.props.facts || {};
		return (
			<React.Fragment>
				{(items !== '') ?
					(<div className="row">
						{items.slice(0, this.props.limit).map((fact, key) =>
							<Fact key={key} fact={fact} width={this.props.width} />
						)}
					</div>) : (<div className="row" style={{ width: '100%' }}><h5 className="p-3">No Results</h5></div>)}
			</React.Fragment>
		)
	};
}

export default MultiFacts;
