import React from 'react';
import ReactDOM from 'react-dom';
import './style.css';
import './grid.css';
import './sizing.css';
import { Home, PlatformPage, FactsPage, PageNotFound, FactPage, GamePage, VideosPage, VideoPage, GamesPage, SearchPage, StudioPage } from './pages';
import { Header, Footer } from './components';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';
import './global';
import * as serviceWorker from './serviceWorker';

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.register();

ReactDOM.render(
  <React.Fragment>
    <div className="fixed-navbar">
      <div className="site">
        <Header />
        <Router basename={process.env.PUBLIC_URL}>
          <Switch>
            {/* Redirect for now. Keep in place until Apr 15th */}
            <Redirect status={301} from="/games/developer/Bungie" to="/studio/bungie" />
            <Redirect status={301} from="/games/publisher/Ubisoft" to="/studio/ubisoft" />
            <Redirect status={301} from="/games/developer/Ubisoft" to="/studio/ubisoft" />
            <Redirect status={301} from="/games/publisher/Capcom" to="/studio/capcom" />
            <Redirect status={301} from="/games/developer/Capcom" to="/studio/capcom" />
            <Redirect status={301} from="/games/publisher/Microsoft%20Studios" to="/studio/microsoft-game-studios" />
            <Redirect status={301} from="/games/publisher/Valve" to="/studio/valve" />
            <Redirect status={301} from="/games/developer/Valve" to="/studio/valve" />
            <Redirect status={301} from="/games/publisher/Nintendo" to="/studio/nintendo" />
            <Redirect status={301} from="/games/developer/Nintendo" to="/studio/nintendo" />
            <Redirect status={301} from="/games/developer/Rare" to="/studio/rare" />
            <Redirect status={301} from="/games/developer/id%20Software" to="/studio/id-software" />
            <Redirect status={301} from="/games/developer/Mojang" to="/studio/mojang" />
            <Redirect status={301} from="/games/publisher/Epic%20Games" to="/studio/epic-games" />
            <Redirect status={301} from="/games/developer/Epic%20Games" to="/studio/epic-games" />
            <Redirect status={301} from="/games/developer/Irrational%20Games" to="/studio/irrational-games" />
            <Redirect status={301} from="/games/publisher/2K" to="/studio/2k-games" />
            <Redirect status={301} from="/games/publisher/Blizzard%20Entertainment" to="/studio/blizzard-entertainment" />
            <Redirect status={301} from="/games/developer/Blizzard%20Entertainment" to="/studio/blizzard-entertainment" />
            <Redirect status={301} from="/games/developer/Bioware" to="/studio/bioware" />
            <Redirect status={301} from="/games/publisher/Electronic%20Arts" to="/studio/electronic-arts" />
            <Redirect status={301} from="/games/publisher/Activision" to="/studio/activision" />
            <Redirect status={301} from="/games/developer/Infinity%20Ward" to="/studio/infinity-ward" />
            <Redirect status={301} from="/games/publisher/Sony%20Interactive%20Entertainment" to="/studio/sony-interactive-entertainment" />
            <Redirect status={301} from="/games/developer/From%20Software" to="/studio/from-software" />
            <Redirect status={301} from="/games/developer/Rockstar%20Games" to="/studio/rockstar-games" />
            <Redirect status={301} from="/games/publisher/Warner%20Bros.%20Interactive%20Entertainment" to="/studio/warner-bros-interactive-entertainment" />
            <Redirect status={301} from="/games/developer/Rocksteady%20Studios" to="/studio/rocksteady-studios" />
            <Redirect status={301} from="/games/platform/ps4" to="/platform/playstation-4" />
            <Redirect status={301} from="/games/platform/xbox_one" to="/platform/xbox-one" />
            <Redirect status={301} from="/games/platform/pc" to="/platform/pc" />
            <Redirect status={301} from="/games/platform/360" to="/platform/xbox-360" />
            <Redirect status={301} from="/games/platform/ps3" to="/platform/playstation-3" />
            <Redirect status={301} from="/games/platform/switch" to="/platform/nintendo-switch" />
            <Redirect status={301} from="/games/platform/ios" to="/platform/ios" />
            <Redirect status={301} from="/games/platform/android" to="/platform/android" />
            {/* Normal Routing */}
            <Route exact path="/" component={Home} />
            <Route path="/fact/:id" component={FactPage} />
            <Route path="/facts/game/:gameslug" render={(props) => <FactsPage {...props} type="game" />} />
            <Route path="/facts" render={(props) => <FactsPage {...props} type="" />} />
            <Route path="/videos/game/:gameslug" render={(props) => <VideosPage {...props} type="game" />} />
            <Route path="/videos/show/:showname" render={(props) => <VideosPage {...props} type="show" />} />
            <Route path="/videos" render={(props) => <VideosPage {...props} type="" />} />
            <Route path="/games" render={(props) => <GamesPage {...props} type="" />} />
            <Route path="/game/:slug" component={GamePage} />
            <Route path="/search/:search" component={SearchPage} />
            <Route path="/video/:slug" component={VideoPage} />
            <Route path="/studio/:studioslug" component={StudioPage} />
            <Route path="/platform/:platformslug" component={PlatformPage} />
            <Route component={PageNotFound} status={404} />            
          </Switch>
        </Router>
        <Footer />
      </div>
    </div>
  </React.Fragment>,
  document.getElementById('root'));
