import React from 'react';
import './multiVideos.css';
import { Video } from '../'

class MultiVideos extends React.Component {

	render() {
		let { items = '' } = this.props.videos || {};
		return (
			<React.Fragment>
				{(items !== '') ?
					(<div className="row">
						{items.slice(0, this.props.limit).map((video, key) =>
							<Video key={key} video={video} width={this.props.width} />
						)}
					</div>) : (<div className="row" style={{ width: '100%' }}><h5 className="p-3">No Results</h5></div>)}
			</React.Fragment>
		)
	};
}

export default MultiVideos;
