import React from 'react';
import './factsPage.css';
import { MultiFacts, Loader } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class FactsPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      factsIsLoaded: false,
      gameIsLoaded: false,
      facts: {},
      game: {},
      limit: 10,
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    if (this.props.type === "") {
      //Load facts
      global.client
        .getEntries({ "order": "-sys.createdAt", "content_type": "fact" })
        .then(entry => {
          this.setState({
            facts: entry,
            factsIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else if (this.props.type === "game") {
      //Load facts
      global.client
        .getEntries({ "content_type": "game", "fields.gameSlug": this.props.match.params.gameslug, "limit": 1 })
        .then(entry => {
          this.setState({
            game: entry,
            gameIsLoaded: true
          })
        })
        .then(entry => global.client
          .getEntries({ "fields.factGame.sys.id": this.state.game.items[0].sys.id, "content_type": "fact" }))
        .then(entry => {
          console.log(entry)
          this.setState({
            facts: entry,
            factsIsLoaded: true
          })
        }).catch(err => console.log(err))
        .catch(err => console.log(err))
    } else {
      console.log(this.props.type)
      this.props.history.push('/')
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleClick = () => {
    this.setState({
      limit: this.state.limit + 10
    })
  }

  render() {
    return (
      <div className="site-content" role="main">
        {this.state.factsIsLoaded ? (
          <React.Fragment>
            <Helmet>
              <title>Facts | Geekotist</title>
              <meta name="description" content="Recent Geek related facts from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:title" property="og:title" content="Facts | Geekotist" />
              <meta name="og:description" property="og:description" content="Recent Geek related facts from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:image" property="og:image" content="https://www.technobuffalo.com/sites/technobuffalo.com/files/styles/xlarge/public/wp/2014/08/assassins-creed-unity.jpg" />
              <meta name="robots" content="index, follow" />
            </Helmet>
            <div>
              <div className="row">
                <h1 className="font-weight-bold p-5">Recent Facts</h1>
                {(this.state.facts) ? (
                  <div>
                    <MultiFacts facts={this.state.facts} limit={this.state.limit} width="half" />
                    {(this.state.facts.items.length > this.state.limit) && (
                      <div className="text-center p-2">
                        <button type="button" className="btn btn-secondary" onClick={this.handleClick} style={{ width: "75%" }}>More Facts</button>
                      </div>
                    )}
                  </div>
                ) : <div className="row" style={{ width: '100%' }}><h5 className="p-3">No Results</h5></div>}
              </div>
            </div>
          </React.Fragment>
        ) : (
            <Loader />
          )}

      </div>
    );
  }
}

export default FactsPage;
