import React from 'react';
import './multiSearchResults.css';
import { Fact, Video, Game } from '..'

class MultiSearchResults extends React.Component {


	render() {
		let { items = '' } = this.props.results || {};
		return (
			<React.Fragment>
				{(items !== '' && items.length > 0) ?
					(<div className="row">
						{items.slice(0, this.props.limit).map((result, key) => {
								if (result.sys.contentType.sys.id === 'fact') {
									return <Fact key={key} fact={result} width="half" />
								} else if(result.sys.contentType.sys.id === 'video') {
									return <Video key={key} video={result} width="half"  />
								} else if(result.sys.contentType.sys.id === 'game') {
									return <Game key={key} game={result} width="half"  />
								} else {
									return ''
								}
							}
						)}
					</div>) : (<div className="row" style={{ width: '100%' }}><h3 className="p-3">No Results</h3></div>)}
			</React.Fragment>
		)
	};
}

export default MultiSearchResults;
