import React from 'react';
import './score.css';

class Score extends React.Component {	
	
	render() {		
		if((this.props.score > 74 || this.props.score > 7.4)) {
		return (
			<span className="review-score positive-score" >{this.props.score}</span>
		)} else if (this.props.score > 49 || this.props.score > 4.9) {
			return(<span className="review-score middle-score" >{this.props.score}</span>)
		} else if (this.props.score > 0 || this.props.score > 0) {
			return(<span className="review-score negative-score" >{this.props.score}</span>)
		} else {
			return(<span className="review-score unknown-score" >N/A</span>)
		}
	}
}

export default Score;