import React from 'react';
import './gamePage.css';
import { GameHeader, MultiFacts, MultiVideos, Loader, GameSidebar, SimilarGamesSidebar } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class FactPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      gameIsLoaded: false,
      factIsLoaded: false,
      videoIsLoaded: false,
      game: {},
      facts: {},
      videos: {},
      video: {},
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
  }

  componentDidMount() {
    //Load game
    global.client
      .getEntries({ "order": "-sys.createdAt", "content_type": "game", "limit": 1, "fields.gameSlug": this.props.match.params.slug })
      .then(entry => {
        console.log(entry)
        this.setState({
          game: entry,
          gameIsLoaded: true
        })
        console.log(entry);
      })
      .then(entry => (
        //Load facts
        global.client
          .getEntries({ "order": "-sys.createdAt", "fields.factGame.sys.id": this.state.game.items[0].sys.id, "content_type": "fact", "limit": 2 })
          .then(entry => {
            this.setState({
              facts: entry,
              factIsLoaded: true
            })
          })
          .catch(err => console.log(err)).then(

            //Load Videos
            global.client
              .getEntries({ "order": "-sys.createdAt", "fields.videoGame.sys.id": this.state.game.items[0].sys.id, "content_type": "video", "limit": 2 })
              .then(entry => {
                this.setState({
                  video: entry.items[Math.floor(Math.random() * (entry.items.length - 1))],
                  videos: entry,
                  videoIsLoaded: true
                })
              })
              .catch(err => console.log(err))
          ))
      )
      .catch(err => console.log(err))
  }

  render() {    
    return (      
      <div className="site-content" role="main">
        {(this.state.gameIsLoaded) ? (
          console.log(this.state.game.items[0]),
          <React.Fragment>
            <Helmet>
              <title>{this.state.game.items[0].fields.gameTitle + ` | Geekotist`}</title>
              <meta name="description" content={this.state.game.items[0].fields.gameDescription} />
              <meta name="og:title" property="og:title" content={this.state.game.items[0].fields.gameTitle + ` | Geekotist`} />
              <meta name="og:description" property="og:description" content={this.state.game.items[0].fields.gameDescription} />
              <meta name="og:image" property="og:image" content={this.state.game.items[0].fields.gameImage.fields.file.url} />
              <meta name="robots" content="index, follow" />
            </Helmet>
            {(this.state.video) ? (<GameHeader video={this.state.video} game={this.state.game.items[0]} />) : (<GameHeader video={{}} game={this.state.game.items[0]} />)}
            <section>
              <div className="container">
                <div className="row">
                  <div className="col-lg-8">
                    <div>
                      <div className="col d-flex align-items-center"><h1 className="font-weight-bold p-0">Facts</h1>{(this.state.factIsLoaded && this.state.facts.items.length > 0) && (<a className="ml-auto font-weight-bold p-0" href={process.env.PUBLIC_URL + "/?p=/facts/game/" + this.state.game.items[0].fields.gameSlug}>More</a>)}</div>
                      {(this.state.factIsLoaded && this.state.facts.items.length > 0) ? (
                        <MultiFacts facts={this.state.facts} limit={2} width={"game"} />
                      ) : (<h5 className="p-3">No Results</h5>)}
                    </div>
                    <div className="py-3">
                      <div className="col d-flex align-items-center"><h1 className="font-weight-bold p-0">Videos</h1>{(this.state.videoIsLoaded && this.state.videos.items.length > 0) && (<a className="ml-auto font-weight-bold p-0" href={process.env.PUBLIC_URL + "/?p=/videos/game/" + this.state.game.items[0].fields.gameSlug}>More</a>)}</div>
                      {(this.state.videoIsLoaded && this.state.videos.items.length > 0) ? (
                        <MultiVideos videos={this.state.videos} limit={2} width={"game"} />
                      ) : (<h5 className="p-3">No Results</h5>)}
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <GameSidebar game={this.state.game.items[0]} />
                    {(this.state.game.items[0].fields.gameSimilar) &&
                      (<SimilarGamesSidebar games={this.state.game.items[0].fields.gameSimilar} />)
                    }
                  </div>
                </div>
              </div>
            </section>
          </React.Fragment>
        ) : (
            <Loader />
          )}
      </div>
    );
  }
}

export default FactPage;
