import React from 'react';
import './header.css';

class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			navBar: 'collapse',
			search: '',
			value: ''
		}

		this.hamburgerClick = this.hamburgerClick.bind(this);
		this.searchClick = this.searchClick.bind(this);
		this.handleChange = this.handleChange.bind(this);
    	this.handleSubmit = this.handleSubmit.bind(this);
	}

	hamburgerClick = () => {
		this.setState({
			navBar: (this.state.navBar === 'collapse') ? 'show' : 'collapse'
		})
	}

	searchClick = () => {
		if(this.state.search === '') {
			this.searchInput.focus(); 
		}
		this.setState({
			search: (this.state.search === '') ? 'active' : '',
			value: ''
		})		
	}

	handleChange = (event) => {
		this.setState({ value: event.target.value });
	}

	handleSubmit = (event) => {
		window.location.href=process.env.PUBLIC_URL  + "/?p=/search/" + this.state.value;
		event.preventDefault();
	}

	render() {
		return (
			<header className="site-header">
				<nav className="navbar navbar-expand-lg navbar-dark bg-dark">
					<div className="container header-mobile">
						<button className="navbar-toggler" onClick={this.hamburgerClick} type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
							<i className="fa fa-bars fa-lg"></i>
						</button>
						<a className="navbar-brand" href="/">Geekotist</a>
						<div className={this.state.navBar + ' navbar-collapse'} id="navbarSupportedContent">
							<ul className="navbar-nav">
								<li className="nav-item">
									<a className="nav-link" href={process.env.PUBLIC_URL + "/"} >Home</a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href={process.env.PUBLIC_URL + "/?p=/facts"} >Facts</a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href={process.env.PUBLIC_URL + "/?p=/videos"} >Videos</a>
								</li>
								<li className="nav-item">
									<a className="nav-link" href={process.env.PUBLIC_URL + "/?p=/games"} >Games</a>
								</li>
							</ul>
						</div>
						<form className={this.state.search + ' navbar-search'} onSubmit={this.handleSubmit}>
							<div className="container">
								<input className="form-control mr-sm-2" type="search" ref={(input) => { this.searchInput = input; }} placeholder="Search for games, facts..." aria-label="Search" value={this.state.value} onChange={this.handleChange} />
								<input type="submit" style={{display: 'none'}} /><i onClick={this.searchClick} className="fa fa-times search-close"></i>
							</div>
						</form>
						<ul className="navbar-nav navbar-right flex-row d-flex align-items-center">
							<li className="nav-item"><span className="nav-link toggle-search" onClick={this.searchClick}><i className="fa fa-search fa-lg"></i></span></li>
						</ul>
					</div>
				</nav>
			</header>


		);
	}
}

export default Header;
