import React from 'react';
import './gameHeader.css';
import { Score } from '..';

class GameHeader extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
		  descriptionLength: 250
		}
		this.readMore = this.readMore.bind(this);
	  }

	readMore= () => {
		this.setState ({
			descriptionLength: this.props.game.fields.gameDescription.length
		})
	}

	render() {
		let { gameImage = '', gameSlug = '', gameDescription = '', gameTitle = '', gameImdb = '', gameMetacritic = '', gameAmazon = '' } = this.props.game.fields || {};
		let { videoSlug = '', videoTitle = '' } = this.props.video.fields || {};
		return (
			<React.Fragment>
				{(gameImage !== '' || gameSlug  !== '' || gameDescription  !== '' || gameTitle  !== '' || gameImdb  !== '' || gameMetacritic  !== '' || gameAmazon  !== '') ?
					(<section className="bg-image bg-dark py-5 py-lg-7 px-4 px-lg-0" style={{ backgroundColor: "#343538" }}>
						<img className="background" src={gameImage.fields.file.url} alt={gameTitle} />
						<div className="container">
							<div className="row align-items-center">
								<div className="col-md-8 order-2 order-md-2 text-center text-md-left">
									<h1 onClick={e => (window.location.href = process.env.PUBLIC_URL + "/?p=/game/" + gameSlug)} className="display-5 font-weight-bold text-white">{gameTitle}</h1>
									<p className="text-light pr-md-5 pr-lg-0">{gameDescription.substring(0, this.state.descriptionLength)}{(gameDescription.length > this.state.descriptionLength) && (<span type="button" className="text-white" onClick={this.readMore}>...Read More</span>)}</p>
									<div className="d-flex d-sm-block flex-column mt-sm-4 pt-2">
										{(videoTitle !== '') ?
											(<a className="btn btn-primary btn-shadow btn-lg" href={process.env.PUBLIC_URL + "/?p=/video/" + videoSlug} role="button"><i className="fa fa-play d-none d-sm-inline mr-1"></i> Watch {videoTitle.substring(0, 43)}{(videoTitle.length > 43) && ('...')}</a>) : (<span></span>)
										}
										<a className="btn btn-outline-light btn-lg mt-2 mt-sm-0 ml-sm-2" href={gameAmazon} role="button">Purchase Now <i className="fa fa-shopping-cart"></i></a>
									</div>
								</div>
								<div className="col-md-4 order-1 order-md-2 d-flex align-items-md-center justify-content-center justify-content-md-end text-center mb-4 mb-md-0">
									<div className="ml-3 d-none d-md-inline-block">
										<p className="font-weight-semibold text-white">MetaCritic</p>
										<Score score={gameMetacritic} />
									</div>
									<div className="ml-3 d-none d-md-inline-block">
										<p className="font-weight-semibold text-white">IMDB</p>
										<Score score={gameImdb} />
									</div>
								</div>
							</div>
						</div>
					</section>) : (<div></div>)
				}
			</React.Fragment>
		)
	};
}

export default GameHeader;
