import React from 'react';
import './similarGamesSidebar.css';

class SimilarGamesSidebar extends React.Component {

	componentWillUnmount() {
		this._isMounted = false;
	}

	render() {
		return (
			<React.Fragment>
				<div class="widget mb-0">
						<div className="widget-header">Popular on Gameforest</div>
							<a className="img-cover img-sm mb-3" href="blog-post.html">
								<img src={this.props.games[0].fields.gameImage.fields.file.url} alt={this.props.games[0].fields.gameImage.fields.title} />
							</a>
							<div className="widget-body">
								<h3 className="h3"><a href={process.env.PUBLIC_URL + "/?p=/game/" + this.props.games[0].fields.gameSlug}>{this.props.games[0].fields.gameTitle}</a></h3>
								<div className="font-size-md font-weight-semibold text-muted mb-2">
									<i className="fa fa-calendar mr-1"></i>
									{(this.props.games[0].fields.gameReleaseDate !== '') ? (new Intl.DateTimeFormat('en-GB', {
													year: "numeric",
													month: "long",
													day: "2-digit"
												}).format(new Date(this.props.games[0].fields.gameReleaseDate))) : ('N/A')}
								</div>
							</div>
						</div>
						{(this.props.games.length > 1) && (
							this.props.games.slice(1, this.props.games.length).map((game, key) =>
								<div className="widget widget-list">
									<div className="media">
										<span className="img-cover">
											<img src={game.fields.gameImage.fields.file.url} alt={game.fields.gameImage.fields.title} />
										</span>
											<div className="media-body">
												<h4><a href={process.env.PUBLIC_URL + "/?p=/game/" + game.fields.gameSlug} title={game.fields.gameTitle}>{game.fields.gameTitle}</a></h4>
												<div className="font-size-sm font-weight-semibold text-muted">
												{(game.fields.gameReleaseDate !== '') ? (new Intl.DateTimeFormat('en-GB', {
													year: "numeric",
													month: "long",
													day: "2-digit"
												}).format(new Date(game.fields.gameReleaseDate))) : ('N/A')}
												</div>
											</div>
									</div>
								</div>
							)
						)}						
			</React.Fragment>
		)
	};
}

export default SimilarGamesSidebar;
