import React from 'react';
import './searchPage.css';
import { Loader, MultiSearchResults } from '../../components';
// import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class SearchPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      resultsLoaded: false,
      results: {},
      limit: 10,
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    //Get fact from ID
    global.client
      .getEntries({ "query": this.props.match.params.search, "sys.type[nin]": "Asset" })
      .then(entry => {
        console.log(entry.items);
        this.setState({
          results: entry,
          resultsLoaded: true
        })
      })
      .catch(err => console.log(err))
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleClick = () => {
    this.setState({
      limit: this.state.limit + 10
    })
  }

  render() {
    return (
      <div className="site-content" role="main">
        {(this.state.resultsLoaded) ? (
          <div className="py-5">
            <React.Fragment>
              {/* <Helmet>
                      <title>{this.state.fact.fields.factTitle + ` - ` + this.state.fact.fields.factBody}</title>
                      <meta name="description" content={this.state.fact.fields.factBody} />
                      <meta name="og:title" property="og:title" content={this.state.fact.fields.factTitle + ` - ` + this.state.fact.fields.factBody} />
                      <meta name="og:description" property="og:description" content={this.state.fact.fields.factBody} />
                      <meta name="og:image" property="og:image" content={this.state.fact.fields.factImage.fields.file.url} />
                      <meta name="robots" content="index, follow" />
                    </Helmet> */}
              <section>
                <div>
                  <div className="row">
                    <h1 className="font-weight-bold p-5">Search Results</h1>
                  </div>
                  <MultiSearchResults results={this.state.results} limit={this.state.limit} width="half" />
                  {(this.state.results.items.length > this.state.limit) && (
                    <div className="text-center p-2">
                      <button type="button" className="btn btn-secondary" onClick={this.handleClick} style={{ width: "75%" }}>More</button>
                    </div>
                  )}
                </div>
              </section>
            </React.Fragment>
          </div>
        ) : (
            <Loader />
          )}
      </div>
    );
  }
}

export default SearchPage;
