import React from 'react';
import './studioPage.css';
import { MultiGames, Loader } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class StudioPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      studioIsLoaded: false,
      gamesIsLoaded: false,
      games: {},
      studio: {},
      limit: 10,
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    //Load games
    global.client
      .getEntries({ "fields.studioSlug": this.props.match.params.studioslug, "content_type": "studios", "limit": 1 })
      .then(entry => {
        this.setState({
          studio: entry,
          studioIsLoaded: true
        })
        console.log(entry);
      })
      .then(entry => global.client
        .getEntries({ "links_to_entry": this.state.studio.items[0].sys.id, "content_type": "game" }))
        .then(entry => {
          console.log(entry)
          this.setState({
            games: entry,
            gamesIsLoaded: true
          })
      }).catch(err => {
        console.log(err);
        this.props.history.push('/');
      })
      .catch(err => {
        console.log(err);
        this.props.history.push('/');
      })
  }


  componentWillUnmount() {
    this._isMounted = false;
  }

  handleClick = () => {
    this.setState({
      limit: this.state.limit + 10
    })
  }

  render() {
    return (
      <div className="site-content" role="main">
        {this.state.studioIsLoaded && this.state.studio && this.state.games && this.state.gamesIsLoaded ? (
          <React.Fragment>
            <Helmet>
              <title>{this.state.studio.items[0].fields.studioName} | Geekotist</title>
              <meta name="description" content="Recent Geek related games from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:title" property="og:title" content={this.state.studio.items[0].fields.studioName + " | Geekotist"} />
              <meta name="og:description" property="og:description" content="Recent Geek related games from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:image" property="og:image" content="https://www.technobuffalo.com/sites/technobuffalo.com/files/styles/xlarge/public/wp/2014/08/assassins-creed-unity.jpg" />
              <meta name="robots" content="index, follow" />
            </Helmet>
            <div>
              <div className="row">
                <h1 className="font-weight-bold p-5">{this.state.studio.items[0].fields.studioName}</h1>
              </div>
              <div className="row">
                <h2 className="font-weight-bold p-5">Games</h2>
              </div>
              <MultiGames games={this.state.games} limit={this.state.limit} width="half" />
              {(this.state.games.items.length > this.state.limit) && (
                <div className="text-center p-2">
                  <button type="button" className="btn btn-secondary" onClick={this.handleClick} style={{ width: "75%" }}>More Games</button>
                </div>
              )}
            </div>
          </React.Fragment>
        ) : (
            <Loader />
          )}

      </div>
    );
  }
}

export default StudioPage;
