import React from 'react';
import './game.css';

class Game extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			width: `col-xl-5`,
			height: '300px',
			isLoaded: false
		}
	}

	componentDidMount() {
		if (this.props.width === "half") {
			this.setState({
				width: `col-xl-5`,
				height: '300px',
				isLoaded: true
			})
		} else if (this.props.width === "content") {
			this.setState({
				width: `col-xl-12`,
				height: '300px',
				isLoaded: true
			})
		} else {
			this.setState({
				// Game Page
				width: `col-xl-12`,
				height: '400px',
				isLoaded: true
			})
		}
	}
	
	render() {
		let { gameTitle = '', gameSlug = '', gameImage = '' } = this.props.game.fields || {};
		return (
			<React.Fragment>			
					{(gameTitle !== '' || gameSlug !== '' || gameImage !== '') ?					
						(<div href={process.env.PUBLIC_URL + "/?p=/game/" + gameSlug} className={`px-0 mx-auto py-2 ${this.state.width}`} onClick={e => (window.location.href = process.env.PUBLIC_URL + "/?p=/game/" + gameSlug)}>
							<div className="d-flex align-items-center position-relative overflow-hidden py-2 py-md-4 px-3 px-xl-1" style={{
								backgroundImage: `url(${gameImage.fields.file.url})`,
								backgroundSize: 'cover',
								backgroundRepeat: 'no-repeat',
								backgroundPosition: 'center',
								minHeight: `${this.state.height}`
							}}>
								<div className="overlay" style={{ backgroundColor: 'rgba(41, 43, 47,  .75)' }}></div>
								<div className="container">
									<div className="row">
										{/* <div classname="lottie">Lottie</div> */}
										<div className="col-md-12 order-2 order-md-1 text-center text-sm-center">
											<h2 className="display-8 font-weight-bold text-white py-2" ><a href={process.env.PUBLIC_URL + "/?p=/game/" + gameSlug}>{gameTitle}</a></h2>
										</div>
									</div>
								</div>
							</div>
						</div>
					) : (<div></div>)}   
			</React.Fragment>
		);
	}
}

export default Game;
