import React from 'react';
import './home.css';
import { MultiFacts, Carousel, Loader, MultiVideos } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');


class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      gameIsLoaded: false,
      factIsLoaded: false,
      videoIsLoaded: false,
      games: {},
      facts: {},
      videos: {},
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
  }

  componentDidMount() {
    //Load Page Info
    global.client
      .getEntries({ "sys.id": "3eEKAb6bVaYVx5cU6KhYay", "content_type": "pageInfo", "limit": 1 })
      .then(entry => {
        console.log(entry);
        this.setState({
          games: entry.items[0].fields,
          gameIsLoaded: true
        })
      })
      .catch(err => console.log(err))
    //Load facts
    global.client
      .getEntries({ "order": "-sys.createdAt", "content_type": "fact", "limit": 4 })
      .then(entry => {
        this.setState({
          facts: entry,
          factIsLoaded: true
        })
      })
      .catch(err => console.log(err))

    //Load Videos
    global.client
      .getEntries({ "order": "-sys.createdAt", "content_type": "video", "limit": 4 })
      .then(entry => {
        this.setState({
          videos: entry,
          videoIsLoaded: true
        })
      })
      .catch(err => console.log(err))

  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    return (
      <div className="site-content" role="main">
        {(this.state.gameIsLoaded || this.state.factIsLoaded || this.state.videoIsLoaded) ? (
          <React.Fragment>
            <Helmet>
              <title>Geekotist | The Geek Egotist</title>
              <meta name="description" content="The Geekotist covers all your pop culture needs, so you can know everything there is to know about your favourite Games, TV and Movies. Facts, Videos and Quotes so you can get be a Geekotist (Geek Egotist)." />
              <meta name="og:title" property="og:title" content="Geekotist | The Geek Egotist" />
              <meta name="og:description" property="og:description" content="The Geekotist covers all your pop culture needs, so you can know everything there is to know about your favourite Games, TV and Movies. Facts, Videos and Quotes so you can get be a Geekotist (Geek Egotist)." />
              <meta name="og:image" property="og:image" content="https://www.technobuffalo.com/sites/technobuffalo.com/files/styles/xlarge/public/wp/2014/08/assassins-creed-unity.jpg" />
              <meta name="robots" content="index, follow" />
            </Helmet>

            {(this.state.games && this.state.gameIsLoaded) ? (
              <Carousel games={this.state.games} />
            ) : <div></div>}
            <div className={`py-3`} ></div>
            <div className="">
              <div className="row">
                <div className="col-12 col-md-8 text-center mx-auto mb-4">
                  <i className="fa fa-question icon"></i>
                  <h2 className="font-weight-bold font-size-lg">Recent Facts from the Geekotist</h2>
                  <p className="lead">Are you sure you know everything about your favourite game</p>
                </div>
                {(this.state.facts && this.state.factIsLoaded) ? (
                  <div>
                    <MultiFacts facts={this.state.facts} limit={4} width="half" />
                    <div className="text-center p-3">
                      <button type="button" className="btn btn-secondary" onClick={e => (window.location.href = process.env.PUBLIC_URL + "/?p=/facts")} style={{ width: "90%" }}>More Facts</button>
                    </div>
                  </div>
                ) : <h5 className="p-3">No Results</h5>}
              </div>
              <div className={`py-3`} ></div>
              <div>
                <div className="col-12 col-md-8 text-center mx-auto mb-4">
                  <i className="fa fa-play icon"></i>
                  <h2 className="font-weight-bold font-size-lg">Recent Videos from the Geekotist</h2>
                  <p className="lead">Are you sure you know everything about your favourite game</p>
                </div>
                {(this.state.videos && this.state.videoIsLoaded) ? (
                  <div>
                    <MultiVideos videos={this.state.videos} limit={4} width="half" />
                    <div className="text-center p-3">
                      <button type="button" className="btn btn-secondary" onClick={e => (window.location.href = process.env.PUBLIC_URL + "/?p=/videos")} style={{ width: "90%" }}>More Videos</button>
                    </div>
                  </div>
                ) : <h5 className="p-3">No Results</h5>}
              </div>
            </div>
          </React.Fragment>
        ) : (
            <Loader />
          )}
      </div>
    )
  };
}

export default Home;
