import React from 'react';
import './video.css';

class Fact extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			width : `col-xl-5`,
			isLoaded: false
		}
	}

	componentDidMount() {
		if (this.props.width === "half") {
			this.setState({
				width : `col-xl-5`,
				height : '300px',
				playerButtonHeight : '',
				textSize: '',
				isLoaded: true
			})
		} else if (this.props.width  === "game") {
			this.setState({
				width : `col-xl-5`,
				height : '300px',
				playerButtonHeight : 'game-video-height',
				textSize: 'font-size-xs',
				isLoaded: true
			})
		} else {
			this.setState({
				width : `col-xl-10`,
				height : '600px',
				playerButtonHeight : '',
				textSize : '',
				isLoaded: true
			})
		}
	}
	
	render() {
		let { videoTitle = '', videoImageMediumQuality = '', videoImageMaxQuality = '', videoSlug = '' } = this.props.video.fields || {};
		return (
			<React.Fragment>				
					{(videoTitle !== '' || videoImageMediumQuality !== '' || videoSlug !== '' || videoImageMaxQuality !== '') ?						
					(<div className={`px-0 mx-auto py-2 ${this.state.width}`} >
						<div className="embed-player" style={{border: '1px solid black'}}>
							{(this.props.width === "game" || this.props.width === "half") ? (
								<img style={{width: '100%', height: '100%'}} src={videoImageMediumQuality} href={process.env.PUBLIC_URL  + "/?p=/video/" + videoSlug} alt={videoTitle} />
							) : (<img style={{width: '100%', height: '100%'}} src={videoImageMaxQuality} href={process.env.PUBLIC_URL  + "/?p=/video/" + videoSlug} alt={videoTitle} />)}
							<div className={`embed-icon embed-icon-dark ${this.state.playerButtonHeight}`}>
								<span href={process.env.PUBLIC_URL  + "/?p=/video/" + videoSlug} onClick={e  => (window.location.href=process.env.PUBLIC_URL  + "/?p=/video/" + videoSlug)}><i className="fa fa-play" aria-hidden="true"></i></span>
							</div>
							{(!(this.props.width  === "game")) && (
								<div className="embed-caption">
									<a href={process.env.PUBLIC_URL  + "/?p=/video/" + videoSlug} className={'embed-title ' + this.state.textSize}>{videoTitle}</a>
								</div>
							)}							
						</div>
						{((this.props.width  === "game")) && (<h6 style={{padding: '5px'}}><a href={process.env.PUBLIC_URL  + "/?p=/video/" + videoSlug}>{videoTitle}</a></h6>)}
					</div>
					) : (<div></div>)} 
			</React.Fragment>
		);
	}
}

export default Fact;
