import React from 'react';
import './carousel.css';
import OwlCarousel from 'react-owl-carousel';

class Carousel extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			responsive: {
				0: {
					items: 1,
				},
				600: {
					items: 2,
				},
				1000: {
					items: 3,
				},
			}
		}
	}

	render() {
		let { featuredGames = '' } = this.props.games || {};
		return (
			<React.Fragment>
				{(featuredGames !== '') ?
					(<section className="bg-darken p-1">
						<div className="container-fluid px-0">
							<OwlCarousel
								className="owl-carousel"
								loop
								margin={4}
								dots={false}
								responsive={this.state.responsive}
								autoplay={true}
							>
								{featuredGames.slice(0, 4).map((game, key) =>
										<div key={key} className="owl-img owl-img-games item" onClick={e => (window.location.href = process.env.PUBLIC_URL + "/?p=/game/" + game.fields.gameSlug)}>
											<img className="" style={{ maxHeight: '100%', maxWidth: '100%' }} src={game.fields.gameImage.fields.file.url} alt={game.fields.gameTitle} />
											<div className="owl-caption owl-caption-bottom">
												<h1 className="owl-title bg-darken text-white"><a href={process.env.PUBLIC_URL + "/?p=/game/" + game.fields.gameSlug}>{game.fields.gameTitle}</a></h1>
											</div>
										</div>
								)}
							</OwlCarousel>

						</div>
					</section>) : (<div></div>)}
			</React.Fragment>
		)
	};
}

export default Carousel;
