import React from 'react';
import './multiGames.css';
import { Game } from '../'

class MultiGames extends React.Component {
	
	render() {
		let { items = '' } = this.props.games || {};
		return (
			<div className="row">
				{(items !== '') ?	
					(items.slice(0, this.props.limit).map((game, key) =>					
						<Game key={key} game={game} width="half"  />
					)
					) : (<div className="row" style={{ width: '100%' }}><h5 className="p-3">No Results</h5></div>)}
			</div>
		)
	};
}

export default MultiGames;
