import React from 'react';
import './videosPage.css';
import { MultiVideos, Loader, Video } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');


class VideosPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      videosIsLoaded: false,
      gameIsLoaded: false,
      videos: {},
      video: {},
      game: {},
      limit: 10,
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    if (this.props.type === "") {
      //Load videos
      global.client
        .getEntries({ "order": "-sys.createdAt", "content_type": "video" })
        .then(entry => {
          this.setState({
            video: entry.items[Math.floor(Math.random() * entry.items.length)],
            videos: entry,
            videosIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else if (this.props.type === "game") {
      //Load facts
      global.client
        .getEntries({ "content_type": "game", "fields.gameSlug": this.props.match.params.gameslug, "limit": 1 })
        .then(entry => {
          this.setState({
            game: entry,
            gameIsLoaded: true
          })
        })
        .then(entry => global.client
          .getEntries({ "fields.videoGame.sys.id": this.state.game.items[0].sys.id, "content_type": "video" }))
        .then(entry => {
          console.log(entry)
          this.setState({
            video: entry.items[Math.floor(Math.random() * entry.items.length)],
            videos: entry,
            videosIsLoaded: true
          })
        }).catch(err => console.log(err))
        .catch(err => console.log(err))
    } else if (this.props.type === "show") {
      //Load videos
      global.client
        .getEntries({ "order": "-sys.createdAt", "content_type": "video", "fields.videoShow": this.props.match.params.showname })
        .then(entry => {
          this.setState({
            video: entry.items[Math.floor(Math.random() * entry.items.length)],
            videos: entry,
            videosIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else {
      console.log(this.props.type)
      this.props.history.push('/')
    }
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleClick = () => {
    this.setState({
      limit: this.state.limit + 10
    })
  }

  render() {
    return (
      <div className="site-content" role="main">
        {this.state.videosIsLoaded && this.state.video ? (
          <React.Fragment>
            <Helmet>
              <title>Videos | Geekotist</title>
              <meta name="description" content="Recent Geek related videos from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:title" property="og:title" content="Videos | Geekotist" />
              <meta name="og:description" property="og:description" content="Recent Geek related videos from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:image" property="og:image" content="https://www.technobuffalo.com/sites/technobuffalo.com/files/styles/xlarge/public/wp/2014/08/assassins-creed-unity.jpg" />
              <meta name="robots" content="index, follow" />
            </Helmet>
            <section className="bg-image py-0 py-lg-5" ya-style="background-color: #2e2f2f">
              <img className="background" src={this.state.video.fields.videoImageMaxQuality} alt={this.state.video.fields.videoTitle} />
              <div className="container">
                <div className="row">
                  <div className="col" >
                    <Video video={this.state.video} width="other" />
                  </div>
                </div>
              </div>
            </section>
            <div>
              <div className="row">
                <h1 class="font-weight-bold p-5">Recent Videos</h1>
                <MultiVideos videos={this.state.videos} limit={this.state.limit} width="half" />
                {(this.state.videos.items.length > this.state.limit) && (
                  <div className="text-center p-2" style={{ width: '100%', marginRight: 'auto !important', marginLeft: 'auto !important' }}>
                    <button type="button" className="btn btn-secondary" onClick={this.handleClick} style={{ width: "90%" }}>More Videos</button>
                  </div>
                )}
              </div>
            </div>
          </React.Fragment>
        ) : (
            <Loader />
          )}

      </div>
    );
  }
}

export default VideosPage;
