import React from 'react';
import './fact.css';

class Fact extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			width : `col-xl-5`,
			height : '300px',
			isLoaded: false
		}		
	}

	componentDidMount() {
		if (this.props.width === "half") {
			this.setState({
				width : `col-xl-5`,
				height : '300px',
				textSize: '',
				displaySize: 'display-5',
				isLoaded: true
			})
		} else if (this.props.width  === "game") {
			this.setState({
				width : `col-xl-5`,
				height : '300px',
				textSize: 'font-size-sm',
				displaySize: 'display-1',
				isLoaded: true
			})
		} else {
			this.setState({
				// Fact Page
				width : `col-xl-12`,
				height : '400px',
				textSize: '',
				displaySize: 'display-5',
				isLoaded: true
			})
		}
	}
	
	render() {
		let { factImage = '', factTitle = '', factBody = '' } = this.props.fact.fields || {};
		let { id = '' } = this.props.fact.sys || {};
		return (
			<React.Fragment>			
					{(factTitle !== '' || factBody !== '' || id !== '' || factImage !== '') ?					
						(<div className={`px-0 mx-auto py-2 ${this.state.width}`} href={process.env.PUBLIC_URL  + "/?p=/fact/" + id} onClick={e  => (window.location.href=process.env.PUBLIC_URL  + "/?p=/fact/" + id)}>
							<div className="d-flex align-items-center position-relative overflow-hidden py-2 py-md-4 px-3 px-xl-1" style={{
								backgroundImage: `url(${factImage.fields.file.url})`,
								backgroundSize: 'cover',
								backgroundRepeat: 'no-repeat',
								backgroundPosition: 'center',
								minHeight: `${this.state.height}`
							}}>
								<div className="overlay" style={{ backgroundColor: 'rgba(41, 43, 47,  .75)' }}></div>
								<div className="container">
									<div className="row">
										{/* <div classname="lottie">Lottie</div> */}
										<div className="col-md-12 order-2 order-md-1 text-center text-sm-left">
											<span className="badge badge-dark font-size-xs" >FACT</span>
											<h2 className={'font-weight-bold text-white py-2 ' + this.state.displaySize + this.state.textSize}>{factTitle}</h2>
											<p className={'text-white ' + this.state.textSize}>{factBody}</p>
										</div>
									</div>
								</div>
							</div>
						</div>
					) : (<div></div>)}   
			</React.Fragment>
		);
	}
}

export default Fact;
