import React from 'react';
import './factPage.css';
import { MultiFacts, GameSidebar, Fact, Loader } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class FactPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      factIsLoaded: false,
      factsIsLoaded: false,
      fact: {},
      facts: {},
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
  }

  componentDidMount() {
    //Get fact from ID
    global.client
      .getEntries({ "sys.id": this.props.match.params.id, "content_type": "fact", "include": 2 })
      .then(entry => {
        this.setState({
          fact: entry.items[0],
          factIsLoaded: true
        })
      })
      .catch(err => console.log(err))

    //Get recent facts
    global.client
      .getEntries({ "order": "-sys.createdAt", "content_type": "fact", "limit": 4 })
      .then(entry => {
        this.setState({
          facts: entry,
          factsIsLoaded: true
        })
      })
      .catch(err => console.log(err))
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    return (
      <div className="site-content" role="main">
        {(this.state.factIsLoaded) ? (
          <div>
            <React.Fragment>
              <Helmet>
                <title>{this.state.fact.fields.factTitle + ` - ` + this.state.fact.fields.factBody}</title>
                <meta name="description" content={this.state.fact.fields.factBody} />
                <meta name="og:title" property="og:title" content={this.state.fact.fields.factTitle + ` - ` + this.state.fact.fields.factBody} />
                <meta name="og:description" property="og:description" content={this.state.fact.fields.factBody} />
                <meta name="og:image" property="og:image" content={this.state.fact.fields.factImage.fields.file.url} />
                <meta name="robots" content="index, follow" />
              </Helmet>
              <section>
                <div className="container">
                  <h1 className="font-weight-bold p-0">Fact</h1>
                  <div className="row">
                    <div className="col-lg-8">
                      <Fact fact={this.state.fact} width='full' />
                      <div className="post-single">
                        <div className="post-header post-header-author">
                          <div>
                            <div className="post-meta">
                              <span className="post-meta-item"><i className="fa fa-calendar"></i> {(new Intl.DateTimeFormat('en-GB', {
                                year: "numeric",
                                month: "long",
                                day: "2-digit"
                              }).format(new Date(this.state.fact.fields.factDate)))}</span>
                            </div>
                          </div>
                        </div>
                      </div>
                      <div>
                        <div className="col d-flex align-items-center"><h3 className="font-weight-bold p-0">Share</h3></div>
                        <a style={{ margin: '5px' }} className="btn btn-social btn-icon-left btn-twitter font-size-sm" href={`https://twitter.com/share?url=https://geekotist.com/?p=/fact/` + this.state.fact.fields.factSlug + `&text=` + this.state.fact.fields.factBody + `&via=Geekotist`} role="button" aria-label="twitter"><i className="fa fa-twitter"></i> Twitter</a>
                        <a style={{ margin: '5px' }} className="btn btn-social btn-icon-left btn-reddit font-size-sm" href={`https://reddit.com/submit?url=https://geekotist.com/?p=/fact/` + this.state.fact.fields.factSlug + `&title=` + this.state.fact.fields.factBody} role="button" aria-label="reddit"><i className="fa fa-reddit-alien"></i> Reddit</a>
                        <a style={{ margin: '5px' }} className="btn btn-social btn-icon-left btn-pinterest font-size-sm" href={`https://pinterest.com/pin/create/link/?url=https://geekotist.com/?p=/fact/` + this.state.fact.fields.faceSlug + `&description=` + this.state.fact.fields.factBody + `&media=` + this.state.fact.fields.factImage.fields.file.url} role="button" aria-label="pinterest"><i className="fa fa-pinterest"></i> Pinterest</a>
                      </div>
                      <div className="py-5">
                        <div className="col d-flex align-items-center"><h1 className="font-weight-bold p-0">Recent Facts</h1><a className="ml-auto font-weight-bold p-0" href={process.env.PUBLIC_URL + "/?p=/facts/"}>More</a></div>
                        {(this.state.factsIsLoaded && this.state.facts.items.length > 0) ? (
                          <MultiFacts facts={this.state.facts} limit={4} width={"game"} />
                        ) : (<h5 className="p-3">No Results</h5>)}
                      </div>
                    </div>
                    <div className="col-lg-4 py-2">
                      <GameSidebar game={this.state.fact.fields.factGame[0]} />
                    </div>
                  </div>
                </div>
              </section>
            </React.Fragment>
          </div>
        ) : (
            <Loader />
          )}
      </div>
    );
  }
}

export default FactPage;
