import React from 'react';
import './videoPage.css';
import { Loader, GameSidebar, MultiVideos } from '../../components';
import { Helmet } from 'react-helmet';
import { documentToHtmlString } from '@contentful/rich-text-html-renderer';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');
let HtmlToReactParser = require('html-to-react').Parser;

class VideoPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      videoIsLoaded: false,
      videosIsLoaded: false,
      videos: {},
      video: {},
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
  }

  componentDidMount() {
    //Load Video
    global.client
      .getEntries({ "order": "-sys.createdAt", "fields.videoSlug": this.props.match.params.slug, "content_type": "video", "limit": 1, "include": 2 })
      .then(entry => {
        this.setState({
          video: entry.items[0],
          videoIsLoaded: true
        })
      })
      .catch(err => console.log(err))

    //Load Videos
    global.client
      .getEntries({ "order": "-sys.createdAt", "fields.videoSlug[nin]": this.props.match.params.slug, "content_type": "video", "limit": 4 })
      .then(entry => {
        this.setState({
          videos: entry,
          videosIsLoaded: true
        })
      })
      .catch(err => console.log(err))
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  render() {
    let htmlToReactParser = new HtmlToReactParser();
    return (
      <div className="site-content" role="main">
        {this.state.videoIsLoaded ? (
          <React.Fragment>
            <Helmet>
              <title>{this.state.video.fields.videoTitle + ` | Geekotist`}</title>
              <meta name="description" content={this.state.video.fields.videoDescription} />
              <meta name="og:title" property="og:title" content={this.state.video.fields.videoTitle + ` | Geekotist`} />
              <meta name="og:description" property="og:description" content={this.state.video.fields.videoDescription} />
              <meta name="og:image" property="og:image" content={this.state.video.fields.videoImage} />
              <meta name="og:video" property="og:video" content={this.state.video.fields.videoEmbed} />
              <meta name="robots" content="index, follow" />
            </Helmet>
            <section className="bg-image py-0 py-lg-5" ya-style="background-color: #2e2f2f">
              <img className="background" src={this.state.video.fields.videoImageMaxQuality} alt={this.state.video.fields.videoTitle} />
              <div className="container">
                <div className="row">
                  <div className="col" style={{
                    position: "relative",
                    paddingBottom: "54.00%" /* 16:9 */,
                    paddingTop: 25,
                    height: 0
                  }}>
                    <iframe
                      style={{
                        position: "absolute",
                        top: 0,
                        left: 0,
                        width: "100%",
                        height: "100%"
                      }}
                      src={this.state.video.fields.videoEmbed + '?autoplay=1'}
                      frameBorder='0'
                      allow='autoplay; encrypted-media'
                      allowFullScreen
                      title='video'
                    />
                  </div>
                </div>
              </div>
            </section>
            <section className="py-lg-5">
              <div className="container">
                <div className="row">
                  <div className="col-lg-8">
                    <div className="post post-single">
                      <div className="post-header post-header-author">
                        <div>
                          <h1 className="post-title">{this.state.video.fields.videoTitle}</h1>
                          <div className="post-meta">
                            <span className="post-meta-item"><i className="fa fa-calendar"></i> {(new Intl.DateTimeFormat('en-GB', {
                              year: "numeric",
                              month: "long",
                              day: "2-digit"
                            }).format(new Date(this.state.video.fields.videoDate)))}</span>
                            <span className="post-meta-item"><i className="fa fa-television"></i><a href={process.env.PUBLIC_URL + "/?p=/videos/show/" + this.state.video.fields.videoShow[0]}>{this.state.video.fields.videoShow[0]}</a></span>
                          </div>
                        </div>
                      </div>
                      <div className="post-body">
                        {htmlToReactParser.parse(documentToHtmlString(this.state.video.fields.videoDescription))}
                      </div>
                    </div>
                    <div>
                      <div className="col d-flex align-items-center"><h3 className="font-weight-bold p-0">Share</h3></div>
                      <a style={{ margin: '5px' }} className="btn btn-social btn-icon-left btn-twitter font-size-sm" href={`https://twitter.com/share?url=https://geekotist.com/?p=/video/` + this.state.video.fields.videoSlug + `&text=` + this.state.video.fields.videoTitle + `&via=Geekotist`} role="button" aria-label="twitter"><i className="fa fa-twitter"></i> Twitter</a>
                      <a style={{ margin: '5px' }} className="btn btn-social btn-icon-left btn-reddit font-size-sm" href={`https://reddit.com/submit?url=https://geekotist.com/?p=/video/` + this.state.video.fields.videoSlug + `&title=` + this.state.video.fields.videoTitle} role="button" aria-label="reddit"><i className="fa fa-reddit-alien"></i> Reddit</a>
                      <a style={{ margin: '5px' }} className="btn btn-social btn-icon-left btn-pinterest font-size-sm" href={`https://pinterest.com/pin/create/link/?url=https://geekotist.com/?p=/video/` + this.state.video.fields.videoSlug + `&description=` + this.state.video.fields.videoTitle + `&media=` + this.state.video.fields.videoImageMaxQuality} role="button" aria-label="pinterest"><i className="fa fa-pinterest"></i> Pinterest</a>
                    </div>
                    <div className="py-5">
                      <div className="col d-flex align-items-center"><h1 className="font-weight-bold p-0">Recent Videos</h1><a className="ml-auto font-weight-bold p-0" href={process.env.PUBLIC_URL + "/?p=/videos/"}>More</a></div>
                      {(this.state.videosIsLoaded && this.state.videos.items.length > 0) ? (
                        <MultiVideos videos={this.state.videos} limit={4} width={"game"} />
                      ) : (<h5 className="p-3">No Results</h5>)}
                    </div>
                  </div>
                  <div className="col-lg-4">
                    <GameSidebar game={this.state.video.fields.videoGame[0]} />
                  </div>
                </div>
              </div>
            </section>

          </React.Fragment>
        ) : (
            <Loader />
          )}
      </div>
    );
  }
}

export default VideoPage;
