import React from 'react';
import './gamesPage.css';
import { GameHeader, MultiGames, Loader } from '../../components';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
ReactGA.initialize('UA-112755747-1');

class GamesPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      gamesIsLoaded: false,
      games: {},
      game: {},
      limit: 10,
      error: null
    }
    ReactGA.pageview(process.env.PUBLIC_URL + window.location.pathname + window.location.search);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    console.log(this.props.type)
    if (this.props.type === "") {
      //Load games
      global.client
        .getEntries({ "order": "-sys.createdAt", "content_type": "game" })
        .then(entry => {
          this.setState({
            game: entry.items[Math.floor(Math.random() * (entry.items.length - 1))],
            games: entry,
            gamesIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else if (this.props.type === "platform") {
      //Load games
      global.client
        .getEntries({ "order": "-sys.createdAt", "content_type": "game", "fields.gamePlatform": this.props.match.params.platformslug })
        .then(entry => {
          this.setState({
            game: entry.items[Math.floor(Math.random() * (entry.items.length - 1))],
            games: entry,
            gamesIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else if (this.props.type === "developer") {
      //Load games
      global.client
        .getEntries({ "fields.gameDeveloper": this.props.match.params.developerslug, "order": "-sys.createdAt", "content_type": "game" })
        .then(entry => {
          this.setState({
            game: entry.items[Math.floor(Math.random() * (entry.items.length - 1))],
            games: entry,
            gamesIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else if (this.props.type === "publisher") {
      //Load games
      global.client
        .getEntries({ "order": "-sys.createdAt", "content_type": "game", "fields.gamePublisher": this.props.match.params.publisherslug })
        .then(entry => {
          this.setState({
            game: entry.items[Math.floor(Math.random() * (entry.items.length - 1))],
            games: entry,
            gamesIsLoaded: true
          })
        })
        .catch(err => console.log(err))
    } else {
      console.log(this.props.type)
      this.props.history.push('/')
    }
  }


  componentWillUnmount() {
    this._isMounted = false;
  }

  handleClick = () => {
    this.setState({
      limit: this.state.limit + 10
    })
  }

  render() {
    return (
      <div className="site-content" role="main">
        {this.state.gamesIsLoaded && this.state.game ? (
          <React.Fragment>
            <Helmet>
              <title>Games | Geekotist</title>
              <meta name="description" content="Recent Geek related games from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:title" property="og:title" content="Games | Geekotist" />
              <meta name="og:description" property="og:description" content="Recent Geek related games from the Geekotist. Covering all your Fandom related pop culture random facts, including Games, Movies, TV and Comics" />
              <meta name="og:image" property="og:image" content="https://www.technobuffalo.com/sites/technobuffalo.com/files/styles/xlarge/public/wp/2014/08/assassins-creed-unity.jpg" />
              <meta name="robots" content="index, follow" />
            </Helmet>
            <GameHeader game={this.state.game} video={{}} />
            <div>
              <div className="row">
                <h1 className="font-weight-bold p-5">Games</h1>
              </div>
              <MultiGames games={this.state.games} limit={this.state.limit} width="half" />
              {(this.state.games.items.length > this.state.limit) && (
                <div className="text-center p-2">
                  <button type="button" className="btn btn-secondary" onClick={this.handleClick} style={{ width: "75%" }}>More Games</button>
                </div>
              )}
            </div>
          </React.Fragment>
        ) : (
            <Loader />
          )}

      </div>
    );
  }
}

export default GamesPage;
